import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import moment from 'moment';
import { injectIntl, FormattedMessage } from 'react-intl';
import StatusIndicator from 'ui-library/lib/components/general/StatusIndicator';
import LabelValuePairs from 'ui-library/lib/components/layout/LabelValuePairs';
import {
  createStatusIcon,
  createStatusLabel,
  createStatusMessage,
} from '../../../../utils/helpers';
import intlShape from '../../../../utils/intlPropType';

import './AccountHistorySummary.css';

export const AccountHistorySummary = ({
  intl,
  passwordHistory,
}) => {
  const history = passwordHistory && passwordHistory[0];
  const dataPairs = [];
  const filters = ['last-login-time', 'password-expiration-time'];
  const accounStatusLabel = intl.formatMessage({ id: 'components.summaries.password-history.status.account-status.label' });

  if (history) {
    const errors = history['account-usability-errors'];
    if (!_.isEmpty(errors)) {
      errors.forEach((error, index) => {
        const status = createStatusLabel(error['type-name']);
        const icon = createStatusIcon(status);
        dataPairs.push({
          label: (index === 0) ? accounStatusLabel : '',
          value: (
            <StatusIndicator type={StatusIndicator.statusTypes[icon]}>
              <FormattedMessage
                id="components.summaries.password-history.status.account-status.status"
                values={{
                  status,
                }}
              />
            </StatusIndicator>
          ),
        });
        dataPairs.push({
          label: '',
          value: <div className="password-history__message">{createStatusMessage(error.message)}</div>,
        });
      });
    } else {
      dataPairs.push({
        label: accounStatusLabel,
        value: (
          <StatusIndicator type={StatusIndicator.statusTypes.SUCCESS}>
            <FormattedMessage id="components.summaries.password-history.status.account-status.active" />
          </StatusIndicator>
        ),
      });
    }

    filters.forEach((item) => {
      const label = intl.formatMessage({ id: `components.summaries.password-history.status.${item}.label` });
      if (history[item]) {
        const date = `${moment(history[item]).format('YYYY-MM-DD HH:mm:ss')}`;
        dataPairs.push({
          label,
          value: (
            <StatusIndicator type={StatusIndicator.statusTypes.EMPTY}>
              <FormattedMessage
                id={`components.summaries.password-history.status.${item}.date`}
                values={{
                  date,
                }}
              />
            </StatusIndicator>
          ),
        });
      } else {
        dataPairs.push({
          label,
          value: (
            <StatusIndicator type={StatusIndicator.statusTypes.WARNING}>
              <FormattedMessage id={`components.summaries.password-history.status.${item}.non`} />
            </StatusIndicator>
          ),
        });
      }
    });
  } else {
    dataPairs.push({
      label: accounStatusLabel,
      value: (
        <StatusIndicator type={StatusIndicator.statusTypes.WARNING}>
          <FormattedMessage id="components.summaries.password-history.status.account-status.not-active" />
        </StatusIndicator>
      ),
    });
  }

  return (
    <div className="password-history">
      <LabelValuePairs dataPairs={dataPairs} />
    </div>
  );
};

AccountHistorySummary.propTypes = {
  intl: intlShape.isRequired,
  passwordHistory: PropTypes.arrayOf(PropTypes.shape({})),
};

AccountHistorySummary.defaultProps = {
  passwordHistory: undefined,
};

export default injectIntl(AccountHistorySummary);
